<template>
    <page-wrap>
        <!-- 搜索栏 -->
        <search-wrap>
            <el-form ref="form" :model="searchData" label-width="100px" size="mini">
                <el-row :gutter="21">
                    <el-col :span="6">
                        <div class="bg">
                            <el-form-item label="关键词：">
                                <el-input v-model="searchData.keyword" placeholder="请输入内容"></el-input>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="bg">
                            <el-form-item label="文章标题：">
                                <el-input v-model="searchData.title" placeholder="请输入内容"></el-input>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="bg">
                            <el-form-item label="编辑人员：">
                                <el-input v-model="searchData.edit_user_name" placeholder="请输入内容"></el-input>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="6">
                        <div class="bg">
                            <el-form-item label="发布人员：">
                                <el-input v-model="searchData.add_user_name" placeholder="请输入内容"></el-input>
                            </el-form-item>
                        </div>
                    </el-col>



                  </el-row>

                <el-row :gutter="22">
                    <el-col :span="8">
                        <div class="bg">
                            <el-form-item label="编辑日期：">
                                <el-date-picker
                                        v-model="searchData.edit_time"
                                        type="daterange"
                                        value-format="yyyy-MM-dd"
                                        range-separator="至"
                                        start-placeholder="开始日期"
                                        end-placeholder="结束日期">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="8">
                        <div class="bg">
                            <el-form-item label="发布日期：">
                                <el-date-picker
                                        v-model="searchData.add_time"
                                        type="daterange"
                                        value-format="yyyy-MM-dd"
                                        range-separator="至"
                                        start-placeholder="开始日期"
                                        end-placeholder="结束日期">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                    </el-col>
                    <el-col :span="5">
                        <div class="bg">
                            <el-form-item label="状态：">
                                <el-select v-model="searchData.status" placeholder="请选择" value="">
                                    <el-option
                                            v-for="item in status_arr"
                                            :key="item.value"
                                            :label="item.label"
                                            :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </div>
                    </el-col>

                  <el-col :span="3">
                      <el-form-item label-width="0">
                          <el-button type="primary" @click="onSubmit">查询</el-button>
                          <el-button type="danger" @click="onClear">清空</el-button>
                      </el-form-item>
                  </el-col>
                </el-row>
            </el-form>
        </search-wrap>



        <!-- 表格 -->
        <table-wrap>
            <el-table
                    ref="materialkeywordtable"
                    v-loading="loading"
                    :data="tableData"
                    size="mini"
                    @sort-change="tableSortChange"
                    :height="tabTableHeight">
                <el-table-column
                        label="关键词"
                        width="140px"
                        prop="keyword">
                </el-table-column>
                <el-table-column
                        label="分类"
                        width="100px"
                        prop="catename">
                </el-table-column>
                <el-table-column
                        label="绑定学校"
                        width="100px"
                        prop="school_name">
                </el-table-column>
                <el-table-column
                        label="文章"
                        width="100px"
                        prop="title">
                </el-table-column>
                <el-table-column
                        label="完成状态"
                        width="100px"
                        prop="status">
                    <template slot-scope="props">
                        <span v-show="props.row.status == 2" class="check_green">已发布</span>
                        <span v-show="props.row.status == 1" class="check_green">正常</span>
                        <span v-show="props.row.status == 0" class="check_red">已删除</span>
                    </template>
                </el-table-column>
                <el-table-column
                        label="编辑人员"
                        width="120px"
                        prop="edit_user_name">
                </el-table-column>
                <el-table-column
                        label="编辑部门"
                        width="120px"
                        prop="edit_dp_name">
                </el-table-column>
                <el-table-column
                        label="编辑日期"
                        width="140px"
                        prop="edit_time">
                </el-table-column>
                <el-table-column
                        label="发布人员"
                        width="120px"
                        prop="add_user_name">
                </el-table-column>
                <el-table-column
                        label="发布部门"
                        width="120px"
                        prop="add_dp_name">
                </el-table-column>
                <el-table-column
                        label="发布日期"
                        width="140px"
                        prop="add_time">
                </el-table-column>
                <el-table-column label="操作" header-align="center" width="200px">
                    <template slot-scope="scope">
                        <el-button type="text" plain size="mini" icon="el-icon-edit"
                                   @click="onDelete(scope.row,0)">编辑发布
                        </el-button>
                        <el-button type="text" plain size="mini" icon="el-icon-setting"
                                   @click="onShowArticlesLog(scope.row)">历史记录
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </table-wrap>


        <!-- 分页 -->
        <pagination-wrap>
            <new-pagination
                    :total="paginationData.total"
                    :propCurrentPage="paginationData.currentPage"
                    @initPaginationData="showPaginationData"
                    @handleSizeChange="showSizeChange"
                    @handleCurrentChange="showCurrentChange"
            />
        </pagination-wrap>


        <articles-log
                :keyword="articlesID"
                :handle="artilcesLogDialog"/>

    </page-wrap>
</template>

<script>
    import PageWrap from './../../components/pageStructure/PageWrap.vue'
    import SearchWrap from './../../components/pageStructure/SearchWrap.vue'
    import TableWrap from './../../components/pageStructure/TableWrap.vue'
    import paginationWrap from './../../components/pageStructure/paginationWrap.vue'
    import NewPagination from './../../components/base/NewPagination.vue'
    import ArticlesLog from './ArticlesLog.vue'
    import {mapState} from 'vuex'
    import { getDates }  from './../../assets/js/baseFunc/baseSelfFunc'

    export default {
        components: {
            PageWrap, SearchWrap, TableWrap, paginationWrap, NewPagination, ArticlesLog
        },
        data() {
            return {
                searchData: {
                    keyword: '',
                    title: '',
                    add_user_name:'',
                    edit_user_name:'',
                    edit_time:[],
                    status: "",
                    add_time:[],
                },
                tableData: [],
                articlesID : 0,
                artilcesLogDialog:false,
                loading: false,
                openEditmaterial: false,
                paginationData: {},
                status_arr: [
                    {value: 0, label: '删除'},
                    {value: 1, label: '正常'},
                    {value: 2, label: '已发布'},
                ],
            }
        },
        computed: {
            ...mapState('navTabs', [
                'tabTableHeight', 'keyword_to_articles'
            ])
        },
        watch: {
          keyword_to_articles () {
            this.initSearchData()
          }
        },
        mounted() {
            this.initSearchData()
            this.onSubmit()
        },
        methods: {
            initSearchData() {
                this.searchData = {
                    keyword: this.keyword_to_articles.keyword !== '' ? this.keyword_to_articles.keyword : '',
                    title: '',
                    add_user_name:'',
                    edit_user_name:'',
                    edit_time:[],
                    status: "",
                    add_time:[],
                }
            },
            onClear() {
                this.initSearchData()
                this.searchData.keyword = ''
            },
            onSubmit(action = '') {
                let that = this
                if (action !== 'changeCurrentPage') {
                    that.paginationData.currentPage = 1
                }
                if (action !== 'changeSort' && action !== 'changeCurrentPage') {
                    that.paginationData.prop = 'add_time'
                    that.paginationData.order = "descending"
                }
                if (!that.loading) {
                    this.$ajax.post('/material_article/init', {
                        search: that.searchData,
                        paginate: that.paginationData
                    })
                        .then(function (res) {
                            if (res.data !== undefined) {
                                that.tableData = res.data.data
                                that.paginationData.total = res.data.total
                                that.loading = false
//                            that.buttonControl = res.data.buttonControl
                            }
                        })
                        .catch(function (err) {
                            that.$message('网络错误！请联系管理员')
                            that.loading = false
                        })
                }
            },
            tableSortChange(val) {
                this.paginationData.prop = val.prop
                this.paginationData.order = val.order
                this.onSubmit('changeSort')
            },
            showPaginationData(data) {
                this.paginationData.pageSizes = data.pageSizes
                this.paginationData.pageSize = data.pageSize
                this.paginationData.currentPage = data.currentPage
            },
            showSizeChange(val) {
                this.paginationData.pageSize = val
                this.onSubmit()
            },
            showCurrentChange(val) {
                this.paginationData.currentPage = val
                this.onSubmit('changeCurrentPage')
            },
            onShowArticlesLog (row) {
                this.artilcesLogDialog = !this.artilcesLogDialog
                this.articlesID = row.ka_id
            },
            onDelete(row,type) {
                let that = this
                let message = ''
                if (type === 0) {
                    message = '是否停用该记录'
                } else {
                    message = '是否激活该记录'
                }
                this.$confirm(message, '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$ajax.post('/material_keywordlist/material_changetype', {id: row.km_id,type:type})
                        .then(function (res) {
                            if (res.data.type) {
                                that.openEditmaterial = false
                                that.onSubmit()
                                that.$message({
                                    type: 'success',
                                    message: res.data.info
                                })
                            } else {
                                that.$message({type: 'error', message: res.data.info})
                            }
                        })
                        .catch(function (err) {
                            that.$message('网络错误！请联系管理员')
                            that.loading = false
                        })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消操作'
                    });
                });
            },
        }
    }
</script>



