<template>
  <el-dialog
    title="素材录入"
    :visible.sync="isShow"
    width="70%"
    :before-close="handleClose">
    <table class="table1" cellspacing="0">
      <tr>
        <td colspan="3">
          <b>关键词：</b>瑜伽培训
          <b>分类：</b>瑜伽培训
          <b>素材数量：</b>14
        </td>
      </tr>
      <tr>
        <td style="width: 15%;">标题</td>
        <td style="width: 70%;">
          <el-input style="width: 50%;" size="mini"></el-input>
          <span class="red fr" style="margin-right:10px;margin-top: 5px;">未包含关键词</span>
        </td>
        <td rowspan="5" style="width: 15%;" class="buttons">
          <el-button type="primary" plain size="mini">去HTML</el-button>
          <el-button type="primary" plain size="mini">插入图片</el-button>
          <el-button type="primary" plain size="mini">图片代码</el-button>
          <el-button type="primary" plain size="mini">插入关键词</el-button>
          <el-button type="primary" plain size="mini">首关键词加粗</el-button>
          <el-button type="primary" plain size="mini">同义词替换</el-button>
          <el-button type="primary" plain size="mini">回退一步</el-button>
        </td>
      </tr>
      <tr>
        <td>来源作者</td>
        <td>
          <el-input style="width: 50%;" size="mini"></el-input>
        </td>
      </tr>
      <tr style="height: 60px;">
        <td>封面</td>
        <td>
          <el-upload
            style="margin-top: 10px;"
            action="https://jsonplaceholder.typicode.com/posts/">
            <el-button size="small" type="primary">点击上传</el-button>
            <!-- <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div> -->
          </el-upload>
        </td>
      </tr>
      <tr style="height: 150px;">
        <td>正文</td>
        <td>
        </td>
      </tr>
      <tr style="height: 50px;">
        <td>检测结果</td>
        <td>
          <p class="red">关键词密度不足2%;</p>
          <p class="red">与原文相似度超过80%;</p>
        </td>
      </tr>
      <tr style="height: 60px;">
        <td colspan="3">
          <el-radio-group v-model="typeValue" @change="changeTypeValue">
           <el-radio label="xinwen">新闻</el-radio>
           <el-radio label="toutiao">头条</el-radio>
           <el-radio label="zhidao">知道</el-radio>
          </el-radio-group>

          <el-form label-position="right">
            <el-form-item label="地区：" label-width="70px" v-show="typeValue === 'xinwen'">
              <el-input size="mini" style="width: 120px;" v-model="area"></el-input>
            </el-form-item>
            <el-form-item label="悬赏积分：" label-width="100px" v-show="typeValue === 'zhidao'">
              <el-select v-model="integral" style="width: 100px;" size="mini" placeholder="请选择">
                <el-option
                  v-for="item in integralOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>&nbsp;&nbsp;(分)
            </el-form-item>
          </el-form>
        </td>
      </tr>
      <tr>
        <td colspan="3" class="buttons2">
          <el-button type="primary" size="mini">文章检测</el-button>
          <el-button type="primary" size="mini">文章预览</el-button>
          <el-button type="primary" size="mini">保存素材</el-button>
          <el-button type="primary" size="mini">保存发布</el-button>
          <el-button type="primary" size="mini">取消</el-button>
        </td>
      </tr>

    </table>
  </el-dialog>

</template>

<script>
  export default {
    props: {
      show: false
    },
    data () {
      return {
       isShow: false,
       fileList: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],
       typeValue: 'xinwen',
       area: '南京',
       integral: '无悬赏',
       integralOptions: [
         {
            value: '0',
            label: '无悬赏'
          }, {
            value: '10',
            label: '10'
          }, {
            value: '20',
            label: '20'
          }, {
            value: '30',
            label: '30'
          }, {
            value: '40',
            label: '40'
          }, {
            value: '50',
            label: '50'
          }, {
            value: '70',
            label: '70'
          }, {
            value: '100',
            label: '100'
          }
        ]
      }
    },
    watch: {
      show () {
        this.isShow = this.show
      }
    },
    methods: {
      handleClose () {
        this.isShow = false
        this.$emit('close', false)
      },
      changeTypeValue (val) {
        console.log('val', val)
      }
    }
  }
</script>



